import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Menu, MenuItem, Button } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { sortByPrice } from '../../../actions/products'

const StyledMenu = withStyles({
  paper: {
    borderRadius: 0,
    boxShadow: '0 0 4px #e5e5e5'
  },
})(Menu);

const StyledButton = withStyles({
  root: {
    minWidth: 50,
    width: 100,
    height: 50,
    borderRadius: 0,
    '@media (max-width: 959.5px)': {
      width: 50
    },
    '&.MuiButtonBase-root.MuiButton-root.MuiButton-text': {
      padding: 0
    },
    '& .MuiButton-label': {
      display: 'flex',
      justifyContent: 'space-between',
      padding: 5,
      '@media (max-width: 959.5px)': {
        justifyContent: 'center'
      }
    }
  },
  })(({icon, text, ...rest}) => (
    <Button
      aria-controls='sort-menu'
      aria-haspopup='true'
      {...rest}>
      {text}
      {icon}
    </Button>
  ));

const Sort = (props) => {
  const { sortByPrice, label } = props
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (evt) => {setAnchorEl(evt.currentTarget)}
  const handleClose = () => {setAnchorEl(null)}

  const handleSort = (method) => {
    sortByPrice(method)
    handleClose()
  }

  return (
    <>
      <StyledButton
        className='Sort'
        onClick={handleClick}
        text={label}
        icon={<FontAwesomeIcon icon='sort' />}
      />
      <StyledMenu
        id='sort-menu'
        elevation={0}
        getContentAnchorEl={null}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        className='Sort-menu'
        >
        <MenuItem onClick={() => handleSort('low')}>Price: Low - High</MenuItem>
        <MenuItem onClick={() => handleSort('high')}>Price: High - Low</MenuItem>
      </StyledMenu>
    </>
  )
}

const mapDispatchToProps = dispatch => ({
  sortByPrice: (method) => dispatch(sortByPrice(method))
})

export default connect(null, mapDispatchToProps)(Sort)